const mixins = {
  data() {
    return {
      tableColumn: [{
        type: 'checkbox',
        width: 40
      },
      {
        field: 'business_type_name',
        title: '业务类型',
        width: 150
      },
      {
        field: 'brand_name',
        title: '品牌',
        width: 150
      },
      {
        field: 'goods_id',
        title: '商品编号',
        width: 80
      },
      {
        field: 'goods_name',
        title: '商品名称',
        width: 300,
      },
      {
        field: 'batch',
        title: '年周/批次',
        width: 200,
        editRender: {
          autoselect: true,
          name: 'vBatch',
          getParams: (scope) => {
            let _this = this
            return {
              params: {
                company_id: this.params.company_id,
                warehouse_id: this.params.warehouse_id,
                goods_id: scope.row.goods_id,
                bill_type: 2
              }
            }
          },
          events: {
            change: (val, { row }) => {
              if (!row.goods_id) {
                return false
              }
              const {
                warehouse_id,
                warehouse_name,
                allocation_name,
                allocation_id,
                available_stocks,
                batch,
                batch_id
              } = val;
              Object.assign(row, {
                warehouse_id,
                warehouse_name,
                allocation_name,
                allocation_id,
                available_stocks,
                batch,
                batch_id
              });
            }
          }
        }
      },
      {
        field: 'unit_name',
        title: '单位',
        width: 80
      },
      {
        field: 'warehouse_name',
        title: '仓库',
        width: 132
      },
      {
        field: 'allocation_name',
        title: '货位',
        width: 170
      },
      {
        field: 'real_num',
        title: '数量',
        width: 130
      },
      {
        field: 'goods_price',
        title: '原销售价',
        width: 130
      },
      {
        field: 'discount_unit_price',
        title: '原折后价',
        width: 130
      },
      {
        field: 'abrasion_price',
        title: '磨损费',
        width: 130
      },
      {
        field: 'intube_price',
        title: '内垫费',
        width: 130
      },
      {
        field: 'claim_price',
        title: '理赔单价',
        width: 130
      },
      {
        field: 'new_goods_price',
        title: '最新销售价',
        width: 130
      },
      {
        field: 'total_price',
        title: '小计金额',
        width: 130
      },
      {
        field: 'remark',
        title: '备注',
        width: 150,
        showOverflow: true,
        editRender: {
          name: 'Vinput',
          formatter: 'String'
        }
      }
      ],
      tableData: [],
    }
  },
  methods: {
    activeMethod({ column, row, $rowIndex }) {
      if (column.property === 'remark') {
        return true
      }
      if (column.property === "batch") {
        if (row.business_type_name == '三包折款') {

          return false
        } else {
          return true
        }

      }
    },
    addRow() {
      let xtable = this.$refs.xTable;
      xtable
        .createRow({
          production_time: "",
          allocation_id: null,
          batch: this.getWeek()
        })
        .then(newRow => {
          xtable.insertAt(newRow, -1);
        });
      this.referenceDisabled = true;
    },
    footerMethod({
      columns,
      data
    }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (
            ["total_price"].includes(
              column.property
            )
          ) {
            this.totalPrice = this.$utils.sum(data, column.property).toFixed(2);
            return this.$utils.sum(data, column.property).toFixed(2);
          }
          return null;
        })
      ];
    }
  }
}
export default mixins