<template>
  <div>
    <!-- 创建出库单方式：  引用库存订单 -->
    <div class="search_container" v-clock>
      <el-form :inline="true" :model="params" ref="TableDataRef">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules="rules.company_id">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" filterable @focus="DepartmentListInvok">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务员" size="mini">
          <el-input v-model="params.business_name" disabled></el-input>
        </el-form-item>

        <el-form-item label="选择客户" size="mini" prop="customer_id" :rules="rules.customer_id">
          <el-input v-model="params.customer_name" disabled></el-input>
        </el-form-item>

        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="送货需求" size="mini">
          <el-input v-model="params.delivery_demand_name" disabled></el-input>
        </el-form-item>

        <el-form-item label="交货方式" size="mini">
          <el-input v-model="params.hand_over_name" disabled></el-input>
        </el-form-item>

        <el-form-item label="所属区域" size="mini" class="wauto" style="min-width:312px" :rules="[{ required: true,trigger: 'blur' }]">
          <el-input v-model="defaultAreaName" disabled></el-input>
        </el-form-item>

        <el-form-item label="详细地址" size="mini" :rules="[{ required: true }]">
          <el-input v-model="params.address" disabled></el-input>
        </el-form-item>

        <el-form-item label="收货人" size="mini" :rules="[{ required: true }]">
          <el-input v-model="params.consignee_name" disabled></el-input>
        </el-form-item>

        <el-form-item label="手机号" size="mini" :rules="[{ required: true }]">
          <el-input v-model="params.consignee_mobile" disabled></el-input>
        </el-form-item>

        <el-form-item label="出库类型" size="mini">
          <el-input value="三包出库" disabled></el-input>
        </el-form-item>
        <el-form-item label="回款方式" size="mini" prop="payment_method">
          {{(function(){if(params.settlement_customer_id == 0)params.settlement_customer_id = null})()}}
          <el-select v-model="params.payment_method" @change="paymentMethodChange">
            <el-option label="客户自结" :value="1">客户自结</el-option>
            <el-option label="物流代收" :value="2">物流代收</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流公司" size="mini" prop="settlement_customer_id" :rules="rules.settlement_customer_id" v-if='params.payment_method === 2'>
          {{(function(){if(params.settlement_customer_id == 0)params.settlement_customer_id = null})()}}
          <el-select clearable v-model="params.settlement_customer_id" filterable @focus="logisticsInvok" @change="logisticsChanges" :filter-method="logisticsInvok">
            <el-option v-for="item in logisticsList" :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物流公司" size="mini" prop="settlement_customer_id" v-else>
          {{(function(){if(params.settlement_customer_id == 0)params.settlement_customer_id = null})()}}
          <el-select clearable v-model="params.settlement_customer_id" filterable @focus="logisticsInvok" @change="logisticsChanges" :filter-method="logisticsInvok">
            <el-option v-for="item in logisticsList" :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="摘要" size="mini">
          <el-input v-model="params.abstract" type="text"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="quoteOrder" class="addBtn" size="mini">引用单据</el-button>
    </div>
    <div class="table_container">
      <vxe-grid border ref="xTable" show-footer="true" show-overflow='tooltip' size="mini" :footer-method="footerMethod" max-height="500px" :data="tableData" resizable height="500px" column-min-width="100" :mouse-config="{selected: true}" :columns="tableColumn" :keyboard-config="{isArrow: true, isDel: false, isTab: true, isEdit: true}" :edit-config="{trigger: 'click', mode: 'cell',autoClear:false,activeMethod:activeMethod}"></vxe-grid>
    </div>
    <br />
    <el-form :model="params" ref="TableDataRef2" class="priceFoot" :inline="true">
      <el-form-item label="本单使用返利：">
        <!-- 1193需求 Number(params.rebate_balance) <=0 -->
        <el-input v-model="params.rebate_price" type="number" class="element_input" @keyup.native="countSettlementPrice" suffix-icon="el-icon-edit-outline" @change="toFixed2('rebate_price')" :disabled="true"></el-input>
        <span class="yeban">余额:{{params.rebate_balance?params.rebate_balance:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="本单使用授信：">
        <!-- Number(params.credit_balance) <=0 -->
        <el-input v-model="params.credit_price" class="element_input" @keyup.native="countSettlementPrice" type="number" suffix-icon="el-icon-edit-outline" @change="toFixed2('credit_price')" :disabled="true"></el-input>
        <span class="yeban">余额:{{params.credit_balance?params.credit_balance:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="本单使用预收：">
        <!-- Number(params.deposits) <= 0 -->
        <el-input v-model="params.deposits_price" class="element_input" suffix-icon="el-icon-edit-outline" type="number" @change="toFixed2('deposits_price')" @keyup.native="countSettlementPrice" :disabled="true"></el-input>
        <span class="yeban">余额:{{params.deposits?params.deposits:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="客户余额：">
        <el-input v-model="params.balance" class="element_input" disabled></el-input>
        <el-button type="primary" @click="seeMoneyDetail">查看往来</el-button>
      </el-form-item>
      <el-form-item label="本单结算金额：">
        <span>{{params.settlement_price}}</span>
      </el-form-item>
    </el-form>
    <br />
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>
    <div v-if="dialogBillVisible">
      <el-dialog :close-on-click-modal="false" title="选择订单" class="dialogForm" :visible.sync="dialogBillVisible" width="1030px">
        <source-bill @dialogformInline="dialogformInline" @closedialog="closeDialog" :companyId="params"></source-bill>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import sourceBill from "./sourceBill.vue";
import {
  getData,
  repertory,
  opthionList
} from "common/utils/public_function/commonUtils";
import mixin from "./threeoutstorage/threeoutstorage.js";
export default {
  name: "instorageThreeoutstorageThreeoutstorageAddOrder",
  mixins: [mixin],
  data() {
    return {
      authority: {},
      defaultArea: [0], //默认的区域id
      defaultAreaName: "",
      rowIndex: null, //选择的当前表格索引
      params: {},
      tableData: [],
      multipleSelection: [], //已选项
      multiGoodsList: {},
      cityList: [],
      dialogBillVisible: false,
      totalPrice: 0
    };
  },
  components: {
    sourceBill //来源单号
  },
  computed: {
    ...mapGetters(["threeoutstorageAddGet"])
  },
  methods: {
    ...mapActions([
      "threeoutstorageAddACT",
      "threeoutstorageIndexACT",
      "creditmanageAdd_show"
    ]),
    settlement_price(row, column, cellValue, index) {
      if (row.business_type == 1) {
        //理赔单价
        this.$set(
          this.TableData.tableData[index],
          "settlement_price",
          !!-row.claim_price ? -row.claim_price : 0
        );
        return Number(row.settlement_price).toFixed(2);
      } else {
        //数量*最新销售价
        this.$set(
          this.TableData.tableData[index],
          "settlement_price",
          !!(row.goods_num * row.new_goods_price)
            ? Number(row.goods_num) * Number(row.new_goods_price)
            : 0
        );
        return Number(row.settlement_price).toFixed(2);
      }
    },
    confirm(row, index, name) {
      //新增和小强的修改
      if (name == "batch_id") {
        if (Array.isArray(row.batchList)) {
          let selectedBatch = row.batchList.filter(item => {
            return item.batch_id == row.batch_id;
          })[0];
          let arr = [
            "warehouse_id",
            "warehouse_name",
            "allocation_name",
            "allocation_id",
            "batch",
            "batch_id"
          ].forEach(item => {
            this.$set(
              this.TableData.tableData[index],
              item,
              selectedBatch[item]
            );
          });
        }
      }
    },
    getBatchList(row, index) {
      //获取批次
      this.$axMethod
        .getBatchlist({
          company_id: this.params.company_id,
          customer_id: this.params.customer_id,
          goods_id: row.goods_id
        })
        .then(res => {
          this.$set(
            this.TableData.tableData[index],
            "batchList",
            res.data.data
          );
        });
    },
    paymentMethodChange(val) {
      if (this.params.payment_method === 2) {
        setTimeout(() => {
          this.$refs.TableDataRef.validateField(["settlement_customer_id"]);
        }, 50);
      } else {
        this.$refs.TableDataRef.clearValidate("settlement_customer_id");
      }
    },
    goBack() {
      this.$router.push({
        path: "/inventory/threeoutstorage"
      });
    },
    countSettlementPrice() {
      // 本单使用返利+本单使用授信+本单结算金额=订单的合计金额；
      let valArry = ["rebate_price", "credit_price", "deposits_price"];
      valArry.forEach(value => {
        this.$set(
          this.params,
          value,
          !this.params[value] || this.params[value] < 0 ? 0 : this.params[value]
        );
      });
      let settlement_price =
        Number(this.totalPrice) -
        Number(this.params.rebate_price) -
        Number(this.params.credit_price) -
        Number(this.params.deposits_price);
      this.$set(this.params, "settlement_price", settlement_price.toFixed(2));
    },
    toFixed2(val) {
      this.$nextTick(() => {
        if (!this.params[val] || isNaN(this.params[val])) {
          this.params[val] = 0;
        }
        this.params[val] = Number(this.params[val]).toFixed(2);
      });
    },
    seeMoneyDetail() {
      this.$router.push({
        path: "/custom/accountlog"
      });
    },
    getCustomerInfo() {
      //获取客户资料详情
      let params = {
        customer_id: this.params.customer_id,
        company_id: this.params.company_id,
        is_three_packs: 1
      };
      this.$axMethod.getCustomerInfoNEWqx(params).then(({ data }) => {
        this.$set(this.params, "deposits", data.data.deposits);
        this.$set(this.params, "credit_balance", data.data.credit_balance); //授信余额
        this.$set(this.params, "balance", data.data.balance); //客户余额
        this.$set(this.params, "rebate_balance", data.data.rebate_balance); //客户返利余额
        this.countSettlementPrice();
      });
    },
    quoteOrder() {
      this.dialogBillVisible = true;
    },
    closeDialog(val) {
      this.dialogBillVisible = false;
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    dialogformInline(val) {
      let paramJson = {};
      this.logisticsList.push({
        logistics_id: val.intentional_logistics_id,
        logistics_name: val.intentional_logistics
      });
      Object.keys(val).forEach(key => {
        if (key != "goods_info") {
          paramJson[key] = val[key];
        }
      });
      // 优先表头的物流公司
      paramJson.settlement_customer_id = this.params.settlement_customer_id
        ? this.params.settlement_customer_id
        : val.intentional_logistics_id;
      this.defaultAreaName = `${val.province_name}/${val.city_name}/${val.area_name}`;
      this.defaultArea = [val.province_id, val.city_id, val.area_id];
      this.params.cityList = this.defaultArea.join(",");
      this.params.province_id = this.defaultArea[0];
      this.params.city_id = this.defaultArea[1];
      this.params.area_id = this.defaultArea[2];
      opthionList(this, paramJson);
      this.$set(this, "params", Object.assign({}, this.params, paramJson));
      this.tableData = [];
      val.goods_info.forEach((item, i) => {
        this.tableData.push({
          order_goods_id: item.order_goods_id,
          claim_price: item.claim_price,
          business_type: item.business_type,
          business_type_name: item.business_type_name,
          brand_name: item.brand_name,
          goods_id: item.goods_id,
          goods_name: item.goods_name,
          batch_id: item.batch_id,
          batch: item.batch,
          unit_name: item.unit_name,
          warehouse_name: item.warehouse_name,
          warehouse_id: item.warehouse_id ? item.warehouse_id : "",
          allocation_name: item.allocation_name,
          allocation_id: item.allocation_id ? item.allocation_id : "",
          real_num: item.goods_num,
          goods_num: item.goods_num,
          goods_price: item.goods_price,
          discount_unit_price: item.discount_price,
          new_goods_price: item.new_goods_price,
          abrasion_price: item.abrasion_price,
          intube_price: item.intube_price,
          batchList: [
            {
              // 解决回显问题
              batch: item.batch,
              batch_id: item.batch_id
            }
          ],
          total_price: item.total_price,
          settlement_price: item.settlement_price,
          remark: item.remark,
          batchList: [
            {
              // 解决回显问题
              batch: item.batch,
              batch_id: item.batch_id
            }
          ]
        });
      });
      this.dialogBillVisible = false;
      this.getCustomerInfo();
      this.$nextTick(() => {
        this.$refs.xTable.updateFooter();
      });
    },
    submit(formName) {
      let flag = true;
      let vm = this;
      if (!this.params.rebate_price) {
        this.params.rebate_price = 0;
      }
      this.params.goods_info = [];
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.$refs.xTable.getTableData().fullData.length) {
            this.$message.error("请先选择订单");
            return false;
          }
          flag = this.$refs.xTable.getTableData().fullData.every(val => {
            return (
              (val.business_type == 2 && val.batch_id) || val.business_type != 2
            );
          });
          if (!flag) {
            this.$message({
              message: "请选择批次",
              type: "error"
            });
            return false;
          }
          this.$refs.xTable.getTableData().fullData.forEach(item => {
            this.params.goods_info.push({
              order_goods_id: item.order_goods_id,
              goods_id: item.goods_id,
              batch_id: item.batch_id,
              unit_name: item.unit_name,
              warehouse_id: item.warehouse_id,
              warehouse_name: item.warehouse_name,
              allocation_id: item.allocation_id ? item.allocation_id : 0,
              allocation_name: item.allocation_name ? item.allocation_name : "",
              discount_unit_price: item.discount_unit_price,
              unit_credit_price: item.unit_credit_price,
              unit_rebate_price: item.unit_rebate_price,
              unit_deposits_price: item.unit_deposits_price,
              company_id: this.params.company_id,
              company_name: this.params.company_name,
              remark: item.remark,
              real_num: item.real_num,
              claim_price: item.claim_price,
              business_type: item.business_type,
              goods_price: item.goods_price,
              outstock_goods_id: item.outstock_goods_id
                ? item.outstock_goods_id
                : 0,
              batch: item.batch,
              new_goods_price: item.new_goods_price,
              abrasion_price: item.abrasion_price,
              total_price: item.total_price,
              intube_price: item.intube_price
            });
          });
          this.params.goods_info = JSON.stringify(this.params.goods_info);
          let infolist = this.params.goods_info;
          infolist = JSON.parse(infolist);
          infolist = infolist.filter(item => item.business_type == 2);
          if (flag) {
            // 校验安全库存金额参数
            var box = infolist.map(item => {
              return {
                warehouse_id: item.warehouse_id,
                warehouse_name: item.warehouse_name,
                allocation_id: item.allocation_id,
                goods_id: item.goods_id,
                 order_goods_id:item.order_goods_id||0,
                 sale_price:item.new_goods_price||0,
                batch: item.batch,
                batch_id: item.batch_id,
                goods_num: item.real_num
              };
            });
            // 校验对接车郎中
            this.$baseData
              .checkCarDoctor({
                company_id: this.params.company_id,
                company_name: this.params.company_name,
                bill_type: 13,
                // bill_id: this.params.return_id,
                operation_type: 1,
                goods_info: JSON.stringify(box)
              })
              .then(() => {
                // 校验安全库存金额
                this.$axMethod
                  .safetyStockCheck({
                    company_id: this.params.company_id,
                    company_name: this.params.company_name,
                    business_time: this.params.business_time,
                    bill_logogram: "tbck",
                    order_type: 1,
                    goods_info: JSON.stringify(box)
                  })
                  .then(() => {
                    repertory(
                      this,
                      {
                        company_id: this.params.company_id,
                        goods_info: JSON.stringify(infolist),
                        status: 1,
                        type: 3
                      },
                      function() {
                        vm.$axMethod
                          .addThreeOutStorage(vm.params)
                          .then(({ data }) => {
                            if (data.code == 200) {
                              vm.$message({
                                message: data.data,
                                type: "success"
                              });
                              vm.goBack();
                            }
                          });
                      }
                    );
                  });
              });
          }
        }
      });
    },

    companyChange(val) {
      this.tableData = [];
      this.$set(this.params, "department_id", null);
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.params.credit_balance = null; //授信余额
      this.params.deposits = null; //授信余额
      this.params.balance = null; //客户余额
      this.params.rebate_balance = null; //客户返利余额
    }
  },
  created() {
    let { employee_id, company_admin_id } = localStorage;
    if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        company_admin_id: Number(localStorage.company_admin_id)
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: Number(localStorage.company_id),
        department_id: Number(localStorage.department_id),
        business_id: Number(localStorage.employee_id),
        company_admin_id: Number(localStorage.company_admin_id)
      });
    }
    opthionList(this, localStorage);
  }
};
</script>

