<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="库存组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" :disabled="true" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select clearable v-model="params.customer_id" filterable placeholder="请输入" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.order_sn" class="shortSearchInput" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @row-click="selectTableData" max-height="600">
        <el-table-column :show-overflow-tooltip="true" width="50" label="选择">
          <template slot-scope="scope">
            <el-radio v-model="radio" :label="scope.row.order_id">{{null}}</el-radio>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="销售组织" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="order_sn" label="单据号" min-width="148">
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门" min-width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="num" label="商品数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="order_price" label="订单金额" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.order_price}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="credit_price" label="本单使用授信" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.credit_price}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_price" label="本单使用返利" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.rebate_price}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="settlement_price" label="结算金额" align="right" width="120">
          <template slot-scope="scope">
            {{scope.row.settlement_price}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="110">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="abstract" width="180" label="备注">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="[30, 50, 100,500]" @current-change="handleCurrentChange" layout="sizes, prev, pager, next" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="submit()">确 定</el-button>
    </div>
  </div>
</template>

<script>
import {
  getData,
  getInventoryFinanceCompanyOpen,
  getDepartmentListCommon,
  getCustomerList
} from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      radio: null,
      order_id: null,
      timeValue: [],
      searchParams: {},
      TableData: {
        tableData: [],
        total: 10
      },
      is_shop: 0,
      loading: false
    };
  },
  props: ["companyId"],
  methods: {
    selectTableData(val) {
      this.radio = val.order_id;
      this.order_id = val.order_id;
    },
    cancel() {
      this.$emit("closedialog", false);
    },
    submit() {
      if (this.order_id) {
        this.$axMethod
          .referenceOrderInfo({
            order_id: this.order_id,
            order_type: 1
          })
          .then(({ data }) => {
            this.$emit("dialogformInline", data.data);
          });
      } else {
        this.$message.warning("请选择订单");
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    onReset() {
      this.$set(this, "params", {
        order_type: 1,
        company_id: this.companyId.company_id
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "referenceOrderListPublics", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.$set(this, "params", {
      order_type: 1,
      company_id: this.companyId.company_id
    });
    if (!!this.companyId.company_name) {
      this.$nextTick(() => {
        this.companyList = [
          {
            company_id: this.companyId.company_id,
            company_name: this.companyId.company_name
          }
        ];
      });
    } else {
      this.$nextTick(() => {
        this.companyList = [
          {
            company_id: this.companyId.company_id,
            company_name: localStorage.company_name
          }
        ];
      });
    }
    this.$set(this, "searchParams", {
      params: this.params,
      TableData: this.TableData, //列表数据
      API: "referenceOrderListPublics", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

